*, *:before, *:after {
	box-sizing: border-box;
}

html {
	height: 100%;
	font-family: "Roboto Mono",
		-apple-system,
		BlinkMacSystemFont,
		"Segoe UI",
		"Oxygen",
		"Ubuntu",
		"Cantarell",
		"Fira Sans",
		"Droid Sans",
		"Helvetica Neue",
		sans-serif;
	font-size: 100%;
	line-height: 1.5;
}

body {
	min-height: 100%;
	margin: 0;
}

button, input, textarea {
	font: inherit;
}

p {
	margin: 0 0 1.5rem;
}

.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
}

.header-inner,
.section-inner {
	width: 96%;
	max-width: 85rem;
	margin: 0 auto;
}

.header-inner {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 1.5rem 0;
}

.logo-title {
	margin: 0;
	font-size: 1em;
	color: #fff;
}

.logo-title-link {
	color: inherit;
	text-decoration: none;
}

.header-nav-list {
	list-style: none;
	margin: 0;
	padding: 0;
}

.header-nav-list-item {
	display: inline-block;
	margin-left: 1em;
}

.header-nav-list-item-link {
	display: inline-block;
	position: relative;
	color: #fff;
	text-decoration: none;
	padding: .5em .8em;
	font-weight: bold;
}

.header-nav-list-item-link:hover:before {
	content: '';
	background: #14ffb1;
	height: 2px;
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
}

.heroSection {
	background: #181985 url('/assets/img/amoma-hero-bg.png') no-repeat center;
}

.heroSection-inner {
	display: flex;
	align-items: center;
	max-width: 70rem;
	padding: 9rem 0;
}

.heroSection-copy {
	width: 100%;
	max-width: 26em;
}

.heroSection-copy-title {
	margin: 0 0 1.5rem;
	font-size: 2em;
	line-height: 1.2;
	color: #14ffb1;
}

.heroSection-copy-text {
	color: #fff;
}

.heroSection-copy-text-highlight {
	color: #14ffb1;
}

.heroSection-cta {
	display: inline-block;
	color: #fff;
	text-decoration: none;
	background-color: #9400ff;
	font-size: 1.37em;
	font-weight: bold;
	padding: .5em 1.5em;
	border-radius: 50px;
}

.heroSection-cta:hover {
	background-color: #984cfc;
}

.heroSection-illu-image {
	display: block;
	margin: 0 auto;
	width: 100%;
	height: auto;
}

@media(max-width: 60rem) {
	.heroSection-copy {
		margin:0 auto;
		text-align: center;
	}

	.heroSection-inner {
		display: block;
	}

	.heroSection-illu {
		display: none;
	}
}

.servicesSection {
	background: url('/assets/img/services-bg.png') repeat center;
	padding: 4rem 0 0;
}

.servicesSection-title {
	text-align: center;
	margin: 0 0 1.5rem;
	color: #8ea2ac;
	font-size: 2.5em;
}

.services {
	list-style: none;
	padding: 0;
	margin: 0;
	display: grid;
	grid-template-columns: 48% 48%;
	grid-column-gap: 4%;
	grid-row-gap: 3rem;
}

.service {
	background: #fff;
	border: 1px solid #8ea2ac;
	border-radius: 5px;
	padding: 2em;
}

.service-icon {
	display: block;
	margin: 0 auto 3rem;
}

.service-title {
	font-size: 1em;
	text-transform: uppercase;
	color: #9400ff;
	margin: 0 0 1.5rem;
}

.service-list {
	list-style: none;
	padding: 0;
	margin: 0;
}

.service-list-item {
	margin-bottom: 1rem;
}

.servicesSection-illu {
	position: relative;
	height: 230px;
	pointer-events: none;
}

.servicesSection-illu-image {
	display: block;
	position: absolute;
	right: 0;
	bottom: 0;
	pointer-events: none;
}

@media (max-width: 50rem) {
	.services {
		display: block;
		max-width: 24rem;
		margin: 0 auto;
	}

	.service {
		margin-bottom: 3rem;
	}
}

.ctaSection {
	text-align: center;
	background-color: #e5e46e;
	padding: 6rem 0
}

.ctaSection-inner {
	max-width: 45rem;
	margin: 0 auto;
}

.ctaSection-title {
	font-size: 2.5em;
	color: #fff;
	margin: 0 0 1.5rem;
}

.ctaSection-cta {
	display: inline-block;
	color: #fff;
	text-decoration: none;
	background-color: #9400ff;
	font-size: 1.37em;
	font-weight: bold;
	padding: .5em 1.5em;
	border-radius: 50px;
}

.ctaSection-cta:hover {
	background-color: #984cfc;
}

@media (max-width: 38rem) {
	.ctaSection-title {
		font-size: 1.8em;
	}

	.ctaSection-cta {
		font-size: 1em;
	}
}

.contactSection {
	padding: 4rem 0;
}

.contactSection-title {
	margin: 0 0 1.5rem;
	color: #8ea2ac;
	font-size: 2.3em;
	text-align: center;
}

.form {
	max-width: 37rem;
	margin: 0 auto;
}

.form-group {
	margin-bottom: 1.5rem;
}

.form-input {
	display: block;
	width: 100%;
	border: 1px solid #606060;
	border-radius: 5px;
	padding: .5em;
}

.form-buttons {
	text-align: center;
}

.form-button {
	display: inline-block;
	color: #fff;
	background-color: #9400ff;
	border: none;
	font-size: 1.37em;
	font-weight: bold;
	padding: .5em 1.5em;
	border-radius: 50px;
}

.form-button:hover {
	background-color: #984cfc;
}


.footer {
	color: #8ea2ac;
	background-color: #eceff1;
	padding: 1.5em;
}

.footer-inner {
	width: 96%;
	max-width: 85rem;
	margin: 0 auto;
}

.footer-contact {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 1.5rem;
}

.footer-contact-item {
	margin: 0;
}

.footer-contact-link {
	color: inherit;
	text-decoration: none;
}

.footer-contact-inst {
	display: block;
	background: #8ea2ac;
	border-radius: 50%;
	padding: 10px;
}

.footer-contact-inst-image {
	display: block;
}

.footer-contact-inst:hover {
	background: #a3b7bf;
}

.footer-disclaimer {
	text-align: center;
}

.footer-disclaimer-logo {
	margin-right: 1em;
}
